 0;\n    transform: translateY(40px);\n  }\n\n  30%, 70% {\n    opacity: 1;\n    transform: translateY(0px);\n  }\n\n  100% {\n    opacity: 0;\n    transform: translateY(-40px);\n  }\n}\n\n\n@keyframes slide-in {\n  0% {\n    opacity: 0;\n    transform: translateY(40px);\n  }\n  30% {\n    opacity: 1;\n    transform: translateY(0px);\n  }\n}\n\n\n\n@keyframes pulse {\n  0% { transform: scale(1); }\n  10% { transform: scale(1.1); }\n  20% { transform: scale(1); }\n}\n\n\n\n.dropzone, .dropzone * {\n  box-sizing: border-box;\n}\n.dropzone {\n\n  $image-size: 120px;\n\n  $image-border-radius: 20px;\n\n  &.dz-clickable {\n    cursor: pointer;\n\n    * {\n      cursor: default;\n    }\n    .dz-message {\n      &, * {\n        cursor: pointer;\n      }\n    }\n  }\n\n  min-height: 150px;\n  border: 1px solid rgba(0, 0, 0, 0.8);\n  border-radius: 5px;\n  padding: 20px 20px;\n\n  &.dz-started {\n    .dz-message {\n      display: none;\n    }\n  }\n\n  &.dz-drag-hover {\n    border-style: solid;\n    .dz-message {\n      opacity: 0.5;\n    }\n  }\n  .dz-message {\n    text-align: center;\n    margin: 3em 0;\n\n    .dz-button {\n      background: none;\n      color: inherit;\n      border: none;\n      padding: 0;\n      font: inherit;\n      cursor: pointer;\n      outline: inherit;\n    }\n  }\n\n\n\n  .dz-preview {\n    position: relative;\n    display: inline-block;\n\n    vertical-align: top;\n\n    margin: 16px;\n    min-height: 100px;\n\n    &:hover {\n      // Making sure that always the hovered preview element is on top\n      z-index: 1000;\n      .dz-details {\n        opacity: 1;\n      }\n    }\n\n    &.dz-file-preview {\n\n      .dz-image {\n        border-radius: $image-border-radius;\n        background: #999;\n        background: linear-gradient(to bottom, #eee, #ddd);\n      }\n\n      .dz-details {\n        opacity: 1;\n      }\n    }\n\n    &.dz-image-preview {\n      background: white;\n      .dz-details {\n        transition: opacity 0.2s linear;\n      }\n    }\n\n    .dz-remove {\n      font-size: 14px;\n      text-align: center;\n      display: block;\n      cursor: pointer;\n      border: none;\n      &:hover {\n        text-decoration: underline;\n      }\n    }\n\n    &:hover .dz-details {\n      opacity: 1;\n    }\n    .dz-details {\n      $background-color: #444;\n\n      z-index: 20;\n\n      position: absolute;\n      top: 0;\n      left: 0;\n\n      opacity: 0;\n\n      font-size: 13px;\n      min-width: 100%;\n      max-width: 100%;\n      padding: 2em 1em;\n      text-align: center;\n      color: rgba(0, 0, 0, 0.9);\n\n      $width: 120px;\n\n      line-height: 150%;\n\n      .dz-size {\n        margin-bottom: 1em;\n        font-size: 16px;\n      }\n\n      .dz-filename {\n\n        white-space: nowrap;\n\n        &:hover {\n          span {\n            border: 1px solid rgba(200, 200, 200, 0.8);\n            background-color: rgba(255, 255, 255, 0.8);\n          }\n        }\n        &:not(:hover) {\n          span {\n            border: 1px solid transparent;\n          }\n          overflow: hidden;\n          text-overflow: ellipsis;\n        }\n\n      }\n\n      .dz-filename, .dz-size {\n        span {\n          background-color: rgba(255, 255, 255, 0.4);\n          padding: 0 0.4em;\n          border-radius: 3px;\n        }\n      }\n\n    }\n\n    &:hover {\n      .dz-image {\n        // opacity: 0.8;\n        img {\n          transform: scale(1.05, 1.05); // Getting rid of that white bleed-in\n          filter: blur(8px); // Getting rid of that white bleed-in\n        }\n      }\n    }\n    .dz-image {\n      border-radius: $image-border-radius;\n      overflow: hidden;\n      width: $image-size;\n      height: $image-size;\n      position: relative;\n      display: block;\n      z-index: 10;\n\n      img {\n        display: block;\n      }\n    }\n\n\n    &.dz-success {\n      .dz-success-mark {\n        animation: passing-through 3s cubic-bezier(0.770, 0.000, 0.175, 1.000);\n      }\n    }\n    &.dz-error {\n      .dz-error-mark {\n        opacity: 1;\n        animation: slide-in 3s cubic-bezier(0.770, 0.000, 0.175, 1.000);\n      }\n    }\n\n\n    $overlay-color: white;\n    $overlay-bg-color: rgba(0, 0, 0, 0.8);\n\n\n    .dz-success-mark, .dz-error-mark {\n\n      $image-height: 54px;\n      $image-width: 54px;\n\n      pointer-events: none;\n\n      opacity: 0;\n      z-index: 500;\n\n      position: absolute;\n      display: block;\n      top: 50%;\n      left: 50%;\n      margin-left: -(math.div($image-width, 2));\n      margin-top: -(math.div($image-height, 2));\n\n      background: $overlay-bg-color;\n      border-radius: 50%;\n\n      svg {\n        display: block;\n        width: $image-width;\n        height: $image-height;\n        fill: $overlay-color;\n      }\n    }\n    \n      \n    &.dz-processing .dz-progress {\n      opacity: 1;\n      transition: all 0.2s linear;\n    }\n    &.dz-complete .dz-progress {\n      opacity: 0;\n      transition: opacity 0.4s ease-in;\n    }\n\n    &:not(.dz-processing) {\n      .dz-progress {\n        animation: pulse 6s ease infinite;\n      }\n    }\n    .dz-progress {\n      $progress-height: 20px;\n      $progress-border-width: 3px;\n\n      opacity: 1;\n      z-index: 1000;\n\n      pointer-events: none;\n      position: absolute;\n      height: 20px;\n      top: 50%;\n      margin-top: -10px;\n      left: 15%;\n      right: 15%;\n\n      border: $progress-border-width solid $overlay-bg-color;\n      background: $overlay-bg-color;\n\n      border-radius: 10px;\n\n      overflow: hidden;\n\n      .dz-upload {\n        background: $overlay-color;\n\n        display: block;\n        position: relative;\n        height: 100%;\n        width: 0;\n        transition: width 300ms ease-in-out;\n\n        border-radius: $progress-height - $progress-border-width;\n      }\n\n    }\n\n    &.dz-error {\n      .dz-error-message {\n        display: block;\n      }\n      &:hover .dz-error-message {\n        opacity: 1;\n        pointer-events: auto;\n      }\n    }\n\n    .dz-error-message {\n      $width: $image-size + 20px;\n      $color: rgb(177, 6, 6);\n\n      pointer-events: none;\n      z-index: 1000;\n      position: absolute;\n      display: block;\n      display: none;\n      opacity: 0;\n      transition: opacity 0.3s ease;\n      border-radius: 8px;\n      font-size: 13px;\n      top: $image-size + 10px;\n      left: -10px;\n      width: $width;\n      background: $color;\n      padding: 0.5em 1em;\n      color: white;\n\n      // The triangle pointing up\n      &:after {\n        content: '';\n        position: absolute;\n        top: -6px;\n        left: math.div($width, 2) - 6px;\n        width: 0; \n        height: 0; \n        border-left: 6px solid transparent;\n        border-right: 6px solid transparent;\n        border-bottom: 6px solid $color;\n      }\n    }\n\n  }\n}\n\n\n","\r\n@import 'dropzone/src/dropzone';\r\n\r\n.dropzone {\r\n  border: 2px dashed rgba(28,110,164,0.38);\r\n  border-radius: 8px 8px 8px 8px;\r\n}\r\n.dz-image {\r\n  img {\r\n    object-fit: cover;\r\n    height: 100%;\r\n    width: 100%;\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
